import { dataset } from "./data2";
const { routes } = dataset;

const accessToken = 'pk.eyJ1IjoiZXZhbnNtd2VuZHdhIiwiYSI6ImNqMTY3enNlOTAwM28yd25udTZxaWc4dWkifQ.JV8AbBu8MF6o2QC5gctywA';

const map = L.map('map').setView([-1.316903, 36.860536], 14);

L.AwesomeMarkers.Icon.prototype.options.prefix = 'ion';

L.tileLayer('https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token={accessToken}', {
    attribution: '&copy; <a href="https://twiga.ke">Twiga Foods</a> | Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery  <a href="https://www.mapbox.com/">Mapbox</a>',
    maxZoom: 18,
    id: 'mapbox/streets-v11',
    tileSize: 512,
    zoomOffset: -1,
    accessToken
}).addTo(map);

const samplesEl = document.querySelector("#samples");

const resetActive = () => {
    const elems = document.querySelectorAll("li");
    elems.forEach(elem => elem.classList.remove("active"));
    const debugElem = document.querySelector("#debug");
    if (debugElem) {
        debugElem.remove();
    }
}

routes.forEach(({ route_name }, index) => {
    const li = document.createElement("li");
    li.innerText = route_name;

    li.onclick = () => {
        resetActive();
        li.classList.add("active");
        calculateAndDisplayRoute(routes[index].shops)
    }
    samplesEl.appendChild(li);
});

// first item setup
const firstElem = document.querySelector("li");
firstElem.classList.add("active");

const chunkArray = (myArray, chunk_size) => {
    var index = 0;
    var arrayLength = myArray.length;
    var tempArray = [];

    for (index = 0; index < arrayLength; index += chunk_size) {
        const myChunk = myArray.slice(index, index + chunk_size);
        // Do something if you want with the group
        tempArray.push(myChunk);
    }

    return tempArray;
}

const routeControl = L.Routing.control({
    waypoints: [],
    router: L.Routing.mapbox(accessToken),
    fitSelectedRoutes: true,
});

routeControl.addTo(map);

const calculateAndDisplayRoute = (stops) => {
    let shops = stops.slice(0, 25);
    const waypoints = shops.map(({ location }) => (
        L.latLng(location[0], location[1])
    ));

    routeControl.setWaypoints(waypoints);
}

calculateAndDisplayRoute(routes[0].shops);
